import * as dom from "ux/dom";

/**
 * 表示验证状态。
 */
export type Status = "error" | "warning" | "info" | "success" | null;

/**
 * 获取元素的状态。
 * @param elem 要获取的元素。
 * @param classPrefix CSS 类名前缀。
 * @return 返回状态。
 */
export function getStatus(elem: HTMLElement, classPrefix: string) {
    for (const state of ["error", "warning", "info", "success"]) {
        if (dom.hasClass(elem, `${classPrefix}${state}`)) {
            return state as Status;
        }
    }
    return null;
}

/**
 * 设置元素的状态。
 * @param elem 要设置的元素。
 * @param classPrefix CSS 类名前缀。
 * @param value 要设置的状态。
 */
export function setStatus(elem: HTMLElement, classPrefix: string, value: Status) {
    for (const state of ["error", "warning", "info", "success"]) {
        dom.removeClass(elem, `${classPrefix}${state}`);
    }
    if (value) {
        dom.addClass(elem, `${classPrefix}${value}`);
    }
}
